<template>
  <div class="prescriptionInfo">
    <header-bar></header-bar>
    <main>
      <template v-if="tableHeader.orderType==1">
        <section class="th">
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </section>
        <section class="td">
          <span>{{tableHeader.prescriptionNum}}</span>
          <span>{{medItemQr[0].wayNo}}</span>
          <span>{{medItemQr[0].usageNo}}</span>
          <span>{{tableHeader.unitPrice}}</span>
        </section>
        <section class="goods clearfix">
          <div class="box" v-for="(item, index) in medItemQr" :key="index">
            <span>{{item.drugSet.commonName}}</span>
            <span>{{item.number}}{{item.drugSet.dosegeUnit}}</span>
          </div>
        </section>
      </template>
      <template v-else>
        <section class="th">
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </section>
        <section class="td" v-for="(item, index) in medItemQr" :key="index">
          <span>{{item.drugSet.commonName}} {{item.drugSet.dosege}}{{item.drugSet.dosegeUnit}}*{{item.drugSet.divisor}}{{item.drugSet.doseUnit}}/{{item.drugSet.saleUnit}}</span>
          <span>{{item.number}}</span>
          <span>{{item.wayNo}}</span>
          <span>{{item.dose?item.dose:''}}{{item.doseUnit?item.doseUnit:""}}{{item.dose?'/':''}}{{item.usageNo}}</span>
        </section>
      </template>
      <section class="option p-type">
        <span></span>
        <span>{{type}}</span>
      </section>
      <section class="option">
        <span></span>
        <span class="freight">{{postage}}</span>
      </section>
      <div class="sum">
        
        <i>{{orderAmount}}</i>
      </div>
    </main>
    <div class="address" v-if="type==''">
      <div class="info">
        <section>
          <span>{{address.name}}</span>
          <span>{{address.phone}}</span>
          <span>{{address.shippingNo}}</span>
        </section>
        <div
          v-if="address"
          class="address-detail"
        >{{address.province}}{{address.city}}{{address.county}}{{address.addressDetail}}</div>
      </div>
    </div>
    <div class="shop" v-if="type==''">
      <h2></h2>
      <ul class="list">
        <li>
          <section>
            <span>:</span>
            <span>{{address.name}}</span>
          </section>
          <section>
            <span>:</span>
            <span>{{address.phone}}</span>
          </section>
          <section>
            <span>:</span>
            <span>{{address.shippingNo}}</span>
          </section>
          <section>
            <span>:</span>
            <span>{{address.ShippingStatus | typeFilter}}</span>
          </section>
        </li>
      </ul>
    </div>
    <div class="shop" v-else>
      <h2></h2>
      <ul class="list">
        <li v-for="(item, index) in shopList" :key="index">
          <section>
            <span>:</span>
            <span>{{item.drugStoreName}}</span>
          </section>
          <section>
            <span>:</span>
            <span>{{item.address}}</span>
          </section>
          <section>
            <span>:</span>
            <span>{{item.telePhone}}</span>
          </section>
        </li>
        <div class="nodata" v-if="isNo"></div>
        <div class="location" @click="getCity" v-if="isLocation"></div>
      </ul>
    </div>
    <div class="fill"></div>
  </div>
</template>
<script>
import base from "../assets/js/base.js";
import headerBar from "@/components/header-bar";
export default {
  name: "prescriptionInfo",
  data() {
    return {
      tableHeader: {},
      medItemQr: [],
      detail: {},
      address: {},
      isShow: false,
      isNo: false,
      singlePost: null, //
      orderAmount: null,
      type: "",
      postage: 0,
      medRecord: {},
      shopList: [],
      imgArr: [],
      showArr: [],
      isOrder: false,
      isLocation: false,
      addressShow: false
    };
  },
  methods: {
    //
    getShop() {
      if (!sessionStorage.getItem("location")) {
        this.isLocation = true;
        return;
      } else {
        this.isLocation = false;
      }
      base
        .postRequest("/drugStore/findDrugStore", {
          address: sessionStorage.getItem("location"),
          type: 1,
          page: 1,
          rows: 999
        })
        .then(res => {
          if (res.status == 0) {
            for (let index = 0; index < res.data.list.length; index++) {
              res.data.list[index].address = res.data.list[
                index
              ].address.replace(",", "");
            }
            this.shopList = res.data.list;
            if (res.data.list.length > 0) {
              this.isNo = false;
            } else {
              this.isNo = true;
            }
          } else {
            base.toast(res.msg);
          }
        });
    },
    //
    getCity() {
      base.loading();
      var geolocation = new qq.maps.Geolocation();
      geolocation.getIpLocation(e => {
        sessionStorage.setItem("location", e.province + "," + e.city);
        base.closeLoading();
        setTimeout(() => {
          this.getShop();
        }, 100);
      });
    },
    getData() {
      base
        .postRequest("/ptInfo/selectMedOrderInfo", {
          medOrderNo: this.$route.query.medOrderNo,
          medRecordNo: this.$route.query.medRecordNo
        })
        .then(res => {
          if (res.status == 0) {
            this.detail = res.data.ptInfoQr;
            this.medRecord = res.data.medRecord;
            this.imgArr = res.data.mrPictures;
            if (this.imgArr) {
              let newArr = this.imgArr.map(ele => {
                return ele.mrPictureAddress;
              });
              this.showArr = newArr;
            }

            this.tableHeader = res.data.medOrder;
            this.mallNo = res.data.mallOrder.mallNo;
            this.singlePost = res.data.medOrder.orderAmount;
            this.orderAmount = res.data.mallOrder.orderAmount;
            this.medItemQr = res.data.medItemQr;
            this.postage = res.data.postage;
            this.address = res.data.receiptAddress;
            if (
              res.data.mallOrder.shippingStatus == 2 ||
              res.data.mallOrder.shippingStatus == 3
            ) {
              this.type = "";
            } else {
              this.type = "";
            }

            if (
              res.data.mallOrder.shippingStatus == 2 ||
              res.data.mallOrder.shippingStatus == 3
            ) {
              this.getShop();
            }
          } else {
            base.toast(res.msg);
            this.noDataShow = true;
          }
        });
    },
    open() {
      this.isShow = true;
    },
    close() {
      this.isShow = false;
    },
    add() {
      this.$router.push("addAddress");
    },
    onSelect(item) {
      let data = item;
      data.mark = 1;
      base
        .postAjax("/ptInfo/updateReceiptAddress", JSON.stringify(data))
        .then(res => {
          if (res.status == 0) {
            this.isShow = false;
            this.getAdress();
          } else {
            base.toast(res.msg);
          }
        });
    }
  },
  created() {
    this.getData();
    if (this.$route.query.payStatus == 0) {
      this.isOrder = true;
    } else {
      this.isOrder = false;
    }
  },
  filters: {
    typeFilter(e) {
      if (e == 1) {
        return "";
      } else {
        return "";
      }
    }
  },
  components: {
    "header-bar": headerBar
  },
  mounted() {
    base.changeTabbar("#myPrescription");
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
.address {
  position: relative;
  top: ptr(54px);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: ptr(10px) ptr(20px);
  background-color: #fff;
  border-bottom: 1px dotted #f4f4f4;
  .info {
    flex: 1;
    font-size: ptr(14px);
    span {
      margin-right: ptr(20px);
    }
    .noAdress {
      font-size: ptr(30px);
      color: #f44;
    }
    .address-detail {
      color: #666;
      margin-top: ptr(10px);
    }
  }
  .iconfont {
    font-size: ptr(20px);
    text-align: right;
    width: ptr(40px);
  }
}
.p-type {
  margin-top: ptr(40px);
}
.option {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: ptr(10px) ptr(20px);
  background-color: #fff;
  span {
    color: #333;
    font-size: ptr(14px);
  }
  .freight {
    padding-right: ptr(16px);
  }
}
.option:first-of-type {
  padding-top: ptr(40px);
}
.address-modal {
  .bg {
    position: absolute;
    top: 0;
    z-index: 99;
    height: 100%;
    width: 100%;
    opacity: 0.5;
    background-color: #999;
  }
  .content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    z-index: 999;
    padding: ptr(10px);
    height: ptr(460px);
    width: ptr(300px);
    border-radius: ptr(8px);
    background-color: #fff;
    .list {
      height: ptr(400px);
      overflow: auto;
      .info-list {
        padding: ptr(10px);
        font-size: ptr(14px);
        border-bottom: 1px solid #f4f4f4;
        span {
          margin-right: ptr(20px);
        }
        .address-detail {
          color: #666;
          margin-top: ptr(5px);
        }
        .address-active {
          color: #0cb0b5;
          margin-top: ptr(5px);
        }
      }
      .nodata {
        text-align: center;
        color: #999;
        font-size: ptr(14px);
      }
    }
    .active {
      color: #0cb0b5;
    }
    .add {
      margin-top: ptr(20px);
      height: ptr(40px);
      line-height: ptr(40px);
      color: #0cb0b5;
      font-size: ptr(14px);
      text-align: center;
      border-radius: ptr(4px);
      border: 1px solid #0cb0b5;
    }
  }
}
main {
  position: relative;
  top: ptr(44px);
  padding: ptr(10px) ptr(20px);
  background-color: #fff;
  .th {
    color: #fff;
    padding: ptr(5px);
    display: flex;
    font-size: ptr(14px);
    display: -webkit-flex;
    align-items: center;
    justify-content: space-between;
    background-color: #0cb0b5;
    span {
      width: 25%;
      text-align: center;
    }
  }
  .td {
    padding: ptr(5px);
    display: flex;
    display: -webkit-flex;
    align-items: center;
    font-size: ptr(14px);
    justify-content: space-between;
    border: 1px solid #f4f4f4;
    border-top: none;
    span {
      color: #666;
      width: 25%;
      text-align: center;
    }
  }
  .goods {
    border: 1px solid #f4f4f4;
    border-top: none;
    font-size: ptr(14px);
    margin-bottom: ptr(20px);
    span {
      float: left;
      color: #999;
      width: 25%;
      padding: ptr(5px) 0;
      text-align: center;
    }
  }
  .sum {
    font-size: ptr(16px);
    text-align: right;
    padding: ptr(10px);
    i {
      font-size: ptr(18px);
      color: #f44;
    }
  }
}
.shop {
  position: relative;
  top: ptr(44px);
  padding: ptr(20px);
  margin: ptr(5px) 0;
  background-color: #fff;
  h2 {
    font-size: ptr(14px);
    font-weight: 600;
    margin-bottom: ptr(10px);
  }
  section {
    color: #666;
    font-size: ptr(14px);
    margin-bottom: ptr(4px);
  }
  section span:last-child {
    color: #333;
  }
  .list li {
    margin-bottom: ptr(20px);
  }
  .nodata {
    text-align: center;
    color: #999;
    font-size: ptr(14px);
  }
  .location {
    padding-bottom: ptr(10px);
    color: #0cb0b5;
    font-size: ptr(16px);
    text-align: center;
  }
}
.button {
  position: relative;
  top: ptr(54px);
  background-color: #0cb0b5;
  color: #fff;
  width: ptr(200px);
  margin: ptr(20px) auto;
  border-radius: ptr(4px);
  text-align: center;
  height: ptr(35px);
  font-size: ptr(14px);
  line-height: ptr(35px);
}
.fill {
  position: relative;
  top: ptr(44px);
  height: ptr(60px);
  width: 100%;
}
</style>




