import Vue from 'vue'
import Router from 'vue-router'
import MyPrescription from '@/pages/myPrescription'
import Mine from '@/pages/mine'
import EditMine from '@/pages/editMine'
import PrescriptionDetail from '@/pages/prescriptionDetail'
import ShippingAddress from '@/pages/shippingAddress'
import EditAddress from '@/pages/editAddress'
import AddAddress from '@/pages/addAddress'
import MyDoctor from '@/pages/myDoctor'
import MedicalHistory from '@/pages/medicalHistory'
import Information from '@/pages/information'
import Inquiry from '@/pages/inquiry'
import GivenPennants from '@/pages/givenPennants'
import DoctorDetail from '@/pages/doctorDetail'
import MedicalHistoryDetail from '@/pages/medicalHistoryDetail'
import Logistics from '@/pages/logistics'
import ConfirmOrder from '@/pages/confirmOrder'
import PrescriptionInfo from '@/pages/prescriptionInfo'

import base from "../assets/js/base.js";
Vue.use(Router)


let router = new Router({
  routes: [
    {
      path: '/prescriptionInfo',
      name: 'PrescriptionInfo',
      meta: {
        title: "",
      },
      component: PrescriptionInfo
    },
    {
      path: '/myPrescription',
      name: 'MyPrescription',
      meta: {
        title: "",
      },
      component: MyPrescription
    },
    {
      path: '/confirmOrder',
      name: 'ConfirmOrder',
      meta: {
        title: "",
      },
      component: ConfirmOrder
    },
    {
      path: '/logistics',
      name: 'Logistics',
      meta: {
        title: "",
      },
      component: Logistics
    },
    {
      path: '/medicalHistoryDetail',
      name: 'MedicalHistoryDetail',
      meta: {
        title: "",
      },
      component: MedicalHistoryDetail
    },
    {
      path: '/doctorDetail',
      name: 'DoctorDetail',
      meta: {
        title: "",
      },
      component: DoctorDetail
    },
    {
      path:"/prescriptionDetail",
      name:"PrescriptionDetail",
      meta:{
        title:"",
      },
      component:PrescriptionDetail
    },
    {
      path:"/givenPennants",
      name:"GivenPennants",
      meta:{
        title:"",
      },
      component:GivenPennants
    },
    {
      path:"/myDoctor",
      name:"MyDoctor",
      meta:{
        title:""
      },
      component:MyDoctor
    },
    {
      path:"/medicalHistory",
      name:"MedicalHistory",
      meta:{
        title:""
      },
      component:MedicalHistory
    },
    {
      path:"/information",
      name:"Information",
      meta:{
        title:""
      },
      component:Information
    },
    {
      path:"/shippingAddress",
      name:"ShippingAddress",
      meta:{
        title:"",
      },
      component:ShippingAddress
    },
    {
      path:"/editAddress",
      name:"EditAddress",
      meta:{
        title:"",
      },
      component:EditAddress
    },
    {
      path:"/addAddress",
      name:"AddAddress",
      meta:{
        title:"",
      },
      component:AddAddress
    },
    {
      path:"/inquiry",
      name:"Inquiry",
      meta:{
        title:"",
      },
      component:Inquiry
    },
    {
      path: '/mine',
      name: 'Mine',
      meta: {
        title: "",
      },
      component: Mine
    },
    {
      path: '/editMine',
      name: 'EditMine',
      meta: {
        title: "",
      },
      component: EditMine
    },
  ]
});

router.afterEach((to, from, next) => {
  window.scrollTo(0, 0);
});

router.beforeEach((to, from, next) => {
  /* title */
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
});

export default router;


