<template>
  <div>
    <header-bar></header-bar>
    <ul class="header">
      <li>
        <img src="@/assets/img/u60.png" alt="">
      </li>
      <li class="info">
        <section>
          <span class="name">{{info.drName}}</span>
        </section>
        <section>
          <span>{{info.chiefNo}}</span>
          <span class="position">{{info.title}}</span>
        </section>
        <section>
          <span class="position">{{info.hospital}}</span>
        </section>
      </li>
    </ul>
    <main>
      <h3></h3>
      <p>{{info.practiceProfile?info.practiceProfile:""}}</p>
      <h3></h3>
      <section class="th">
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
      </section>
      <section class="td">
        <span></span>
        <span
          v-for="(item, index) in amArr"
          :key="index"
          class="iconfont"
          :class="[item==0?'iconright-f':'iconcircle2yuanquan']"
        ></span>
      </section>
      <section class="td">
        <span></span>
        <span
          v-for="(item, index) in pmArr"
          :key="index"
          class="iconfont"
          :class="[item==0?'iconright-f':'iconcircle2yuanquan']"
        ></span>
      </section>
    </main>
    <div class="fill"></div>
  </div>
</template>
<script>
import base from "../assets/js/base.js";
import headerBar from "@/components/header-bar";
export default {
  name: "doctorDetail",
  data() {
    return {
      info: {},
      drNo: "",
      amArr: [1, 1, 1, 1, 1, 1, 1],
      pmArr: [1, 1, 1, 1, 1, 1, 1]
    };
  },
  methods: {
    getData() {
      this.info = JSON.parse(sessionStorage.getItem("doctorInfo"));
      if (res.data.consultingHour) {
        this.amArr = [];
        this.pmArr = [];
        let dateArr = res.data.consultingHour.split("");
        for (let i = 0; i < dateArr.length; i++) {
          if (i % 2 == 0 || i == 0) {
            this.amArr.push(dateArr[i]);
          } else {
            this.pmArr.push(dateArr[i]);
          }
        }
      }
    }
  },
  components: {
    "header-bar": headerBar
  },
  created() {
    this.drNo = this.$route.query.drNo;
    this.getData();
  },
  mounted() {
    base.changeTabbar("#myDoctor");
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
.header {
  position: relative;
  display: flex;
  display: -webkit-flex;
  top: ptr(44px);
  padding: ptr(10px);
  background-color: #fff;
  border-bottom: 1px solid #f4f4f4;
  img {
    height: ptr(60px);
    width: ptr(60px);
    margin-right: ptr(10px);
  }
  .info {
    flex: 1;
    section {
      margin-bottom: ptr(5px);
      .name {
        font-size: ptr(16px);
        font-weight: bold;
        margin-right: ptr(5px);
      }
      .position {
        font-size: ptr(12px);
        color: #666;
      }
    }
    p {
      color: #999;
    }
  }
}
main {
  position: relative;
  top: ptr(44px);
  padding: ptr(10px) ptr(20px);
  background-color: #fff;
  h3 {
    font-size: ptr(14px);
    margin-bottom: ptr(10px);
  }
  p {
    margin-bottom: ptr(10px);
    font-size: ptr(12px);
    color: #666;
    line-height: ptr(20px);
  }
  .th {
    background-color: #0cb0b5;
    color: #fff;
    padding: ptr(5px) 0;
    font-size: ptr(12px);
    span {
      display: inline-block;
      width: ptr(36px);
      text-align: center;
    }
  }
  .td {
    padding: ptr(5px) 0;
    background-color: #fff;
    color: #666;
    font-size: ptr(12px);
    border-left: 1px solid #ccc;
    border-right: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
    span {
      display: inline-block;
      width: ptr(36px);
      text-align: center;
    }
    .iconfont {
      color: #0cb0b5;
      margin: 0 ptr(2px);
    }
  }
  .wechat {
    position: relative;
    margin-top: ptr(20px);
    width: ptr(70px);
    .button {
      display: inline-block;
      padding: ptr(5px) ptr(10px);
      color: #fff;
      border-radius: ptr(4px);
      background-color: #0cb0b5;
    }
    .tip {
      position: absolute;
      top: ptr(-8px);
      font-size: ptr(12px);
      text-align: center;
      color: #fff;
      right: ptr(-8px);
      height: ptr(20px);
      line-height: ptr(20px);
      width: ptr(20px);
      border-radius: 50%;
      background-color: #f44;
    }
  }
}
.fill {
  position: relative;
  top: ptr(44px);
  height: ptr(60px);
  width: 100%;
}
.iconcircle2yuanquan {
  color: #999 !important;
}
.iconright-f {
  color: #0cb0b5 !important;
}
</style>



