<template>
  <div class="order">
    <header-bar></header-bar>
    <div class="header">
      <section>
        <span class="name">{{detail.name}}</span>
        <span>{{detail.sex}}</span>
        <span>{{detail.birthDay | ageFilter}}</span>
        <span>{{detail.height}}CM</span>
        <span>{{detail.weight}}KG</span>
      </section>
      <section>
        <span>{{detail.phone}}</span>
        <span>{{medRecord.visitDate | timeFilter}}</span>
      </section>
    </div>
    <main>
      <template v-if="tableHeader.orderType==1">
        <section class="th">
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </section>
        <section class="td">
          <span>{{tableHeader.prescriptionNum}}</span>
          <span>{{medItemQr[0].wayNo}}</span>
          <span>{{medItemQr[0].usageNo}}</span>
          <span>{{tableHeader.unitPrice}}</span>
        </section>
        <section class="goods clearfix">
          <div class="box" v-for="(item, index) in medItemQr" :key="index">
            <span>{{item.drugSet.commonName}}</span>
            <span>{{item.number}}{{item.drugSet.dosegeUnit}}</span>
          </div>
        </section>
      </template>
      <template v-else>
        <section class="th">
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </section>
        <section class="td" v-for="(item, index) in medItemQr" :key="index">
          <span>{{item.drugSet.commonName}} {{item.drugSet.dosege}}{{item.drugSet.dosegeUnit}}*{{item.drugSet.divisor}}{{item.drugSet.doseUnit}}/{{item.drugSet.saleUnit}}</span>
          <span>{{item.number}}</span>
          <span>{{item.wayNo}}</span>
          <span>{{item.dose?item.dose:''}}{{item.doseUnit?item.doseUnit:""}}{{item.dose?'/':''}}{{item.usageNo}}</span>
        </section>
      </template>
      <div class="sum">
        
        <i>{{orderAmount}}</i>
      </div>
    </main>
    <div class="fill"></div>
  </div>
</template>
<script>
import base from "../assets/js/base.js";
import headerBar from "@/components/header-bar";
export default {
  name: "prescriptionDetail",
  data() {
    return {
      isShow: true,
      type: "",
      detail: {},
      medRecord: {},
      freight: 0,
      imgArr: [],
      obj: {},
      showArr: [],
      tableHeader: {},
      isOrder: false,
      sum: "",
      medItemQr: [],
      orderAmount: null
    };
  },
  methods: {
    getData() {
      base
        .postRequest("/ptInfo/selectMedOrderInfo", {
          medOrderNo: this.$route.query.medOrderNo,
          medRecordNo: this.$route.query.medRecordNo
        })
        .then(res => {
          if (res.status == 0) {
            this.detail = res.data.ptInfoQr;
            this.medRecord = res.data.medRecord;
            this.imgArr = res.data.mrPictures;
            if (this.imgArr) {
              let newArr = this.imgArr.map(ele => {
                return ele.mrPictureAddress;
              });
              this.showArr = newArr;
            }
            this.tableHeader = res.data.medOrder;
            this.orderAmount = res.data.mallOrder.orderAmount;
            this.medItemQr = res.data.medItemQr;
          } else {
            base.toast(res.msg);
            this.noDataShow = true;
          }
        });
    }
  },
  created() {
    this.getData();
  },
  filters: {
    ageFilter(e) {
      return base.sumAge(e);
    },
    timeFilter(e) {
      if (e) {
        return base.timeSwitch(e);
      }
    }
  },
  components: {
    "header-bar": headerBar
  },
  mounted() {
    base.changeTabbar("#myPrescription");
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
.order {
  background-color: #fff;
}
.header {
  position: relative;
  top: ptr(44px);
  padding: ptr(10px) ptr(20px);
  font-size: ptr(14px);
  color: #666;
  border-bottom: 1px solid #f4f4f4;
  section {
    display: flex;
    display: -webkit-flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: ptr(5px);
    .name {
      color: #333;
      font-size: ptr(16px);
      width: ptr(80px);
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
}
main {
  position: relative;
  top: ptr(44px);
  padding: ptr(10px) ptr(20px);
  background-color: #fff;
  .th {
    color: #fff;
    padding: ptr(5px);
    display: flex;
    font-size: ptr(14px);
    display: -webkit-flex;
    align-items: center;
    justify-content: space-between;
    background-color: #0cb0b5;
    span {
      width: 25%;
      text-align: center;
    }
  }
  .td {
    padding: ptr(5px);
    display: flex;
    display: -webkit-flex;
    align-items: center;
    font-size: ptr(14px);
    justify-content: space-between;
    border: 1px solid #f4f4f4;
    border-top: none;
    span {
      color: #666;
      width: 25%;
      text-align: center;
    }
  }
  .goods {
    border: 1px solid #f4f4f4;
    border-top: none;
    font-size: ptr(14px);
    span {
      float: left;
      color: #999;
      width: 25%;
      padding: ptr(5px) 0;
      text-align: center;
    }
  }
  .sum {
    font-size: ptr(16px);
    text-align: right;
    padding: ptr(10px);
    i {
      font-size: ptr(18px);
      color: #f44;
    }
  }
}
.option {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: ptr(10px) ptr(20px);
  background-color: #fff;
  span {
    color: #333;
    font-size: ptr(14px);
  }
  .freight {
    padding-right: ptr(16px);
  }
}
.option:first-of-type {
  padding-top: ptr(40px);
}
.fill {
  position: relative;
  top: ptr(44px);
  height: ptr(60px);
  width: 100%;
}
</style>




