<template>
    <div>
        <div class="guidance-list" v-for="(item, index) in guidance" :key="index" @click="lookFun(item.medRecordNo,item.medOrderNo)">
            <div class="message">
                <div class="id">:{{item.medRecordNo}}</div>
                <div class="date">:{{item.orderTime | dateFilter}}</div>
            </div>
            <div class="look iconfont iconright" ></div>
        </div>
        <div class="noData" v-if="noDataShow"></div>
        <div class="fill"></div>
    </div>
</template>
<script>
import base from "../assets/js/base.js";
export default {
  name: "medicalHistory",
  data() {
    return {
      guidance: [],
      noDataShow: false
    };
  },
  methods: {
    lookFun(medRecordNo,medOrderNo) {
       setTimeout(() => {
               this.$router.push({path:'medicalHistoryDetail',query:{medRecordNo:medRecordNo,medOrderNo:medOrderNo}});
       }, 300);

    },
    getData() {
      base.postRequest("/ptInfo/selectMedOrderList").then(res => {
        if (res.status == 0) {
          if (res.data.length == 0) {
            this.noDataShow = true;
          } else {
            this.noDataShow = false;
          }
          this.guidance = res.data;
        
        } else {
          base.toast(res.msg);
          this.noDataShow = true;
        }
      });
    }
  },
  filters: {
    dateFilter(val) {
      if(val){
      return base.timeSwitch(val);
      }
    }
  },
  created() {
    this.getData();
  },
  mounted() {
    base.changeTabbar("#medicalHistory");
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
.guidance-list {
  display: flex;
  display: -webkit-flex;
  width: 100%;
  align-items: center;
  background-color: #fff;
  padding: ptr(10px) ptr(20px);
  box-sizing: border-box;
  border-bottom: 1px solid #f4f4f4;
  .message {
    flex: 1;
    font-size: ptr(14px);
    .id {
      margin-bottom: ptr(5px);
      width: ptr(250px);
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .date {
      font-size: ptr(12px);
      color: #666;
    }
  }
  .look {
    color: #999;
    font-size: ptr(20px);
  }
}
.noData {
  padding-top: ptr(50px);
  font-size: ptr(16px);
  color: #999;
  text-align: center;
}
.fill {
  height: ptr(60px);
  width: 100%;
}
</style>




