<template>
  <div>
    <header-bar></header-bar>
    <ul class="header">
      <li>
        <img src="@/assets/img/u60.png" alt="">
      </li>
      <li class="info">
        <section>
          <span class="name">{{info.name}}</span>
          <span class="position">{{info.title}}</span>
        </section>
        <section>
          <span>{{info.chiefNo}}</span>
          <span class="position">{{info.hospital}}</span>
        </section>
      </li>
    </ul>
    <div class="banner-box">
      <img src="@/assets/img/banner.jpg" class="banner" alt="">
      <div class="num">
        <span class="title"></span>
        <input type="number" v-model="num" @input="numInput()">
      </div>
      <div class="sum">
        <span class="title"></span>
        <span class="value">{{sum}}</span>
      </div>
    </div>
    <div class="payType">
      <h3></h3>
      <div class="pay-box">
        <label for="weixinpay">
          <div class="title">
            <i class="iconfont iconweixinzhifu"></i>
          </div>
        </label>
        <input type="radio" name="pay" id="weixinpay" checked>
      </div>
      <!-- <div class="pay-box">
        <label for="zhifupay">
          <div class="title">
            <i class="iconfont iconzhifubaozhifu"></i>
          </div>
        </label>
        <input type="radio" name="pay" id="zhifupay">
      </div>-->

      <p class="tip">: 5 </p>
    </div>
    <div class="give" @click="buy()"></div>
    <div class="fill"></div>
  </div>
</template>
<script>
import base from "../assets/js/base.js";
import headerBar from "@/components/header-bar";
import { Dialog } from 'vant';
export default {
  name: "givenPennants",
  data() {
    return {
      num: 1,
      sum: 5,
      info: {},
      mallOrderNo:""
    };
  },
  methods: {
    numInput() {
      if (this.num > 0) {
        this.sum = this.num * 5;
      } else {
        base.toast("0");
        this.sum = 5;
        return;
      }
    },
    buy() {
      if (this.num <= 0) {
        base.toast("0");
        return;
      }
      let that = this;
      base
        .postRequest("/zlyyptbcjk/buyBanner", {
          ptNo: JSON.parse(sessionStorage.getItem("userinfo")).ptNo,
          drNo: this.$route.query.drNo,
          orderAmount: this.sum
        })
        .then(res => {
          if (res.status == 0) {
            this.mallOrderNo=res.data;
           Dialog.alert({
           title: '',
           message: ''
         }).then(() => {
         location.href="https://www.yizhenyun.net.cn/zlyyptPay/zlyypt/wxpay?mallOrderNo="+this.mallOrderNo+"&path=myDoctor";
         });
          } else {
            base.toast(res.msg);
          }
        });
    },

  },
  components: {
    "header-bar": headerBar
  },
  created() {
    this.info = JSON.parse(sessionStorage.getItem("doctorInfo"));
  },
  mounted() {
    base.changeTabbar("#myDoctor");
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
.header {
  position: relative;
  display: flex;
  display: -webkit-flex;
  top: ptr(44px);
  padding: ptr(10px);
  background-color: #fff;
  border-bottom: 1px solid #f4f4f4;
  img {
    height: ptr(40px);
    width: ptr(40px);
    border-radius: 50%;
    margin-right: ptr(10px);
  }
  .info {
    flex: 1;
    section {
      margin-bottom: ptr(5px);
      .name {
        font-size: ptr(16px);
        font-weight: bold;
        margin-right: ptr(5px);
      }
      .position {
        font-size: ptr(12px);
        color: #666;
      }
    }
    p {
      color: #999;
    }
  }
}
.banner-box {
  position: relative;
  top: ptr(44px);
  padding: ptr(20px);
  text-align: center;
  background-color: #fff;
  .banner {
    height: ptr(80px);
    width: ptr(80px);
  }
  .num,
  .sum {
    margin-top: ptr(20px);
    span {
      font-size: ptr(14px);
    }
    .title {
      font-weight: bold;
    }
    input,
    .value {
      display: inline-block;
      outline: none;
      border: none;
      font-size: ptr(14px);
      text-align: center;
      width: ptr(80px);
      border-bottom: 1px solid #999;
    }
  }
}
.payType {
  position: relative;
  top: ptr(44px);
  padding: ptr(20px);
  background-color: #fff;
  h3 {
    font-size: ptr(14px);
  }
  .pay-box {
    margin-top: ptr(10px);
    padding: ptr(5px);
    display: flex;
    display: -webkit-flex;
    align-items: center;
    justify-content: space-between;
    label {
      flex: 1;
    }
    .iconfont {
      margin-right: ptr(10px);
    }
    .iconweixinzhifu {
      color: #00c800;
    }
    .iconzhifubaozhifu {
      color: #3c9ad7;
    }
    input {
      width: ptr(14px);
      height: ptr(14px);
    }
  }
  .tip {
    padding: ptr(20px) 0;
    font-size: ptr(12px);
    color: #999;
  }
}

.give {
  position: relative;
  top: ptr(44px);
  background-color: #0cb0b5;
  color: #fff;
  width: ptr(200px);
  margin: ptr(20px) auto;
  border-radius: ptr(5px);
  text-align: center;
  height: ptr(40px);
  font-size: ptr(16px);
  line-height: ptr(40px);
}
.fill {
  position: relative;
  top: ptr(44px);
  height: ptr(60px);
  width: 100%;
}
</style>




