<template>
  <div>
    <div
      class="prescription-list"
      v-for="(item, index) in prescription"
      :key="index"
      @click="jumpPage(item.medRecordNo,item.medOrderNo)"
    >
      <div class="message">
        <div class="id">{{item.medOrderNo}}</div>
        <div class="date">{{item.orderTime | dateFilter}}</div>
      </div>
      <!---->
      <template v-if="item.payStatus=='0' && item.orderStatus!='4'">
        <div
          class="select success"
          v-if="item.shippingStatus==0"
          @click.stop="logistics(item.medOrderNo)"
        >
          <i class="iconfont iconwwuliu-"></i>
          <span></span>
        </div>
        <div
          class="select success"
          v-else-if="item.shippingStatus==1"
          @click.stop="logistics(item.medOrderNo)"
        >
          <i class="iconfont icondaifahuo"></i>
          <span></span>
        </div>
        <div
          class="select success"
          v-else-if="item.shippingStatus==2"
          @click.stop="order(item.medRecordNo,item.medOrderNo,item.payStatus)"
        >
          <i class="iconfont icondaifahuo"></i>
          <span></span>
        </div>
        <div
          class="select success"
          v-else-if="item.shippingStatus==3"
          @click.stop="order(item.medRecordNo,item.medOrderNo,item.payStatus)"
        >
          <i class="iconfont icondaifahuo"></i>
          <span></span>
        </div>
      </template>
      <!---->
      <template v-else-if="item.payStatus=='1' && item.orderStatus!='4'">
        <div
          class="select"
          @click.stop="pay(item.medRecordNo,item.medOrderNo)"
        >
          <i class="iconfont iconzhifufangshi"></i>
          <span></span>
        </div>
      </template>
      <!---->
      <template v-else-if="item.orderStatus=='4'">
        <div class="select">
          <i class="iconfont iconwarn"></i>
          <span></span>
        </div>
      </template>
    </div>
    <div class="noData" v-if="noDataShow"></div>
    <div class="fill"></div>
  </div>
</template>
<script>
import base from "../assets/js/base.js";
export default {
  name: "myPrescription",
  data() {
    return {
      getMedicine: true, //
      isVisit: false,
      prescription: [],
      noDataShow: false,
      status: ""
    };
  },
  methods: {
    getData() {
      base.postRequest("/ptInfo/selectMedOrderList").then(res => {
        if (res.status == 0) {
          if (res.data.length == 0) {
            this.noDataShow = true;
          } else {
            this.noDataShow = false;
          }
          this.prescription = res.data;
        } else {
          base.toast(res.msg);
          this.noDataShow = true;
        }
      });
    },
    pay(medRecordNo, medOrderNo) {
      this.$router.push({
        path: "confirmOrder",
        query: {
          medRecordNo: medRecordNo,
          medOrderNo: medOrderNo
        }
      });
    },
    //
    logistics(medOrderNo) {
      this.$router.push({ path: "logistics", query: { medOrderNo: medOrderNo } });
    },
    jumpPage(medRecordNo, medOrderNo) {
      this.$router.push({
        path: "prescriptionDetail",
        query: { medRecordNo: medRecordNo, medOrderNo: medOrderNo }
      });
    },
   order(medRecordNo, medOrderNo,payStatus) {
      this.$router.push({
        name: "PrescriptionInfo",
        query: { medRecordNo: medRecordNo, medOrderNo: medOrderNo,payStatus:payStatus }
      });
    },
  },
  filters: {
    dateFilter(val) {
      if (val) {
        return base.timeSwitch(val, false);
      }
    }
  },
  created() {
    this.getData();
  },
  mounted() {
    base.changeTabbar("#myPrescription");
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
.prescription-list {
  display: flex;
  display: -webkit-flex;
  width: 100%;
  align-items: center;
  background-color: #fff;
  padding: ptr(10px) ptr(20px);
  box-sizing: border-box;
  border-bottom: 1px solid #f4f4f4;
  .select {
    display: flex;
    display: -webkit-flex;
    width: ptr(100px);
    align-items: center;
    flex-direction: column;
    color: #666;
    i {
      font-size: ptr(20px);
    }
    span {
      font-size: ptr(12px);
    }
    .iconweiwancheng {
      color: #ff976a;
    }
    .iconwarn {
      color: #f44;
    }
  }
  .success {
    color: #0cb0b5;
  }
  .message {
    flex: 1;
    padding-left: ptr(5px);
    font-size: ptr(14px);
    .id {
      margin-bottom: ptr(5px);
    }
    .date {
      font-size: ptr(12px);
      color: #666;
    }
  }
  .visit {
    text-align: right;
    font-size: ptr(14px);
    color: #0cb0b5;
    width: ptr(60px);
  }
}
.noData {
  padding-top: ptr(50px);
  font-size: ptr(16px);
  color: #999;
  text-align: center;
}
.fill {
  height: ptr(60px);
  width: 100%;
}
</style>



