<template>
  <div>
    <header-bar></header-bar>
    <ul class="header">
      <li>
        <img src="@/assets/img/u60.png" alt="">
      </li>
      <li class="info">
        <section>
          <span class="name">{{info.name}}</span>
          <span class="position">{{info.title}}</span>
        </section>
        <p></p>
      </li>
    </ul>
    <textarea name="" id="" placeholder="" class="inquiry-content" v-model="subjective"></textarea>
    <div class="uploading clearfix">
      <p><i class="emphasis"></i></p>
      <img :src="item.src" alt="" class="uploadImg" v-for="(item, index) in showImg" :key="index">
      <section class="uploading-img">
        <span class="iconfont iconadd1"></span>
        <span></span>
        <van-uploader :after-read="onRead">
          <van-icon name="photograph"/>
        </van-uploader>
      </section>
    </div>
    <div class="button" @click="submit()"></div>
    <div class="fill"></div>
  </div>
</template>
<script>
import base from "../assets/js/base.js";
import headerBar from "@/components/header-bar";
import $ from 'jquery';
export default {
  name: "inquiry",
  data() {
    return {
      info: {},
      subjective: "",
      columns: [],
      showImg: []
    };
  },
  methods: {
    //
    onRead(files) {
      let that=this;
          lrz( files.file, {
            quality: 0.1   //
          })  
            .then(function(rst) {
                //
                let file=new FormData();
                file.append('file',rst.base64.split(",")[1])
               that.upImg(file)

            }).catch(function(error) {
                base.toast(error.msg);
            })
  
    },
    upImg(file){
      let that=this;
    	$.ajax({
					type: 'post',
					url:  'https://www.yizhenyun.com.cn/yzypt/upload/pictureUpload?medRecordNo='+this.$route.query.medRecordNo,
					data: file,
					dataType: 'json',
					cache: false,
					processData: false,
					contentType: false,
					success: function(res) {
            base.toast(res.msg);
            let obj={};
            obj.src=res.data;
	          that.showImg=[...that.showImg,obj];
          },
          error:function(err){
            base.toast(err.msg || '');
          }
				})
    },
    //
    submit() {
      if (!this.subjective) {
        base.toast("");
        return;
      }
      if (this.showImg.length <= 0) {
        base.toast("");
        return;
      }
      base
        .postRequest("/ptInfo/insertMedOrder", {
          medRecordNo: this.$route.query.medRecordNo,
          subjective: this.subjective,
          drNo: this.info.drNo
        })
        .then(res => {
          if (res.status == 0) {
            base.toast(res.msg);
            setTimeout(() => {
              this.$router.push("myDoctor");
            }, 1000);
          } else {
            base.toast(res.msg);
          }
        });
    }
  },
  components: {
    "header-bar": headerBar
  },
  created() {
    this.info = JSON.parse(sessionStorage.getItem("doctorInfo"));
  },
  mounted() {
    base.changeTabbar("#myDoctor");
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
.header {
  position: relative;
  display: flex;
  display: -webkit-flex;
  top: ptr(44px);
  height: ptr(64px);
  padding: ptr(10px);
  box-sizing: border-box;
  background-color: #fff;
  border-bottom: 1px solid #f4f4f4;
  img {
    height: ptr(40px);
    width: ptr(40px);
    border-radius: 50%;
    margin-right: ptr(10px);
  }
  .info {
    flex: 1;
    section {
      margin-bottom: ptr(5px);
      .name {
        font-size: ptr(16px);
        font-weight: bold;
        margin-right: ptr(5px);
      }
      .position {
        font-size: ptr(12px);
        color: #666;
      }
    }
    p {
      color: #999;
    }
  }
}
.select-box {
  position: relative;
  top: ptr(44px);
  width: 100%;
  padding: ptr(10px);
  box-sizing: border-box;
  background-color: #fff;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #f4f4f4;
  .title {
    font-size: ptr(14px);
    width: ptr(80px);
  }
  .value {
    flex: 1;
    height: ptr(20px);
    line-height: ptr(20px);
    background-color: #eee;
    border-radius: ptr(4px);
    padding: ptr(5px) ptr(10px);
  }
}
.inquiry-content {
  position: relative;
  top: ptr(44px);
  width: 100%;
  padding: ptr(10px);
  height: ptr(200px);
  outline: none;
  border: none;
  resize: none;
  overflow-y: auto;
  box-sizing: border-box;
  border-bottom: 1px solid #f4f4f4;
}
.uploading {
  position: relative;
  top: ptr(41px);
  padding: ptr(10px);
  background-color: #fff;
  p {
    font-size: ptr(12px);
    color: #999;
    margin-bottom: ptr(10px);
  }
  .emphasis{
    color: #f44!important;
  }
  .uploadImg {
    float: left;
    height: ptr(80px);
    width: ptr(80px);
    margin: 0 ptr(4px) ptr(4px) 0;
  }
  .uploading-img {
    float: left;
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    height: ptr(80px);
    width: ptr(80px);
    text-align: center;
    background-color: #eee;
    .iconfont {
      font-size: ptr(20px);
      margin-bottom: ptr(5px);
    }
    span {
      color: #999;
    }
    .imgFile {
      position: absolute;
      opacity: 0;
      height: ptr(80px);
      width: ptr(80px);
    }
  }
}
.button {
  position: relative;
  top: ptr(44px);
  background-color: #0cb0b5;
  color: #fff;
  width: ptr(200px);
  margin: ptr(20px) auto;
  border-radius: ptr(5px);
  text-align: center;
  height: ptr(40px);
  font-size: ptr(16px);
  line-height: ptr(40px);
}
.fill {
  position: relative;
  top: ptr(44px);
  height: ptr(60px);
  width: 100%;
}
</style>




