<template>
  <div>
    <header-bar></header-bar>
    <div class="box">
      <ul class="info">
        <li class="shippingNo">
          :
          <span class="text">{{shippingNo?shippingNo:''}}</span>
        </li>
        <li>:{{shippingCompany?shippingCompany:''}}</li>
      </ul>
      <span
        class="btn"
        v-clipboard:copy="shippingNo"
        v-clipboard:success="onCopy"
        v-clipboard:error="onError"
      ></span>
    </div>
  </div>
</template>
<script>
import base from "../assets/js/base.js";
import headerBar from "@/components/header-bar";
export default {
  name: "logistics",
  data() {
    return {
      shippingCompany:"",
      shippingNo:""
    };
  },
  methods: {
    // 
    onCopy(e) {
      base.toast("");
    },
    // 
    onError(e) {
      base.toast("");
    },
    getData() {
      base.postRequest("/ptInfo/selectShipping",{
        orderNo:this.$route.query.medOrderNo
      }).then(res => {
        if (res.status == 0) {
          this.shippingCompany=res.data.shippingCompany;
          this.shippingNo=res.data.shippingNo;
        } else {
          base.toast(res.msg);
        }
      });
    }
  },
  components: {
    "header-bar": headerBar
  },

  created() {
    this.getData();
  },
  mounted() {
    base.changeTabbar("#myPrescription");
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
.box {
  position: relative;
  top: ptr(44px);
  padding: ptr(10px) ptr(20px);
  display: flex;
  background-color: #fff;
}
.shippingNo{
  margin-bottom: ptr(20px);
}
.info {
  font-size: ptr(14px);
}
.btn {
  flex: 1;
  text-align: right;
}
.van-steps--vertical {
  position: relative;
  top: ptr(44px);
}
</style>



