<template>
  <div>
    <header-bar></header-bar>
    <div class="header">
      <section>
        <span class="name">{{detail.name}}</span>
        <span>{{detail.sex}}</span>
        <span>{{detail.birthDay | ageFilter}}</span>
        <span>{{detail.height}}CM</span>
        <span>{{detail.weight}}KG</span>
      </section>
      <section>
        <span>{{detail.phone}}</span>
        <span>{{medRecord.visitDate | timeFilter }}</span>
      </section>
    </div>
    <main v-if="isShow">
      <h3>S </h3>
      <p>{{medRecord.subjective}}</p>
      <h3>O </h3>
      <p>
        <img v-for="(item, index) in showArr" :key="index" :src="item" alt="" @click="imgShow()">
      </p>
      <h3>A </h3>
      <p>{{medRecord.diagContentName}}</p>
      <h3>P </h3>
      <p></p>
      <div class="look" @click="look()"></div>
    </main>
    <main v-else>
      <template v-if="tableHeader.orderType==1">
        <section class="th">
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </section>
        <section class="td">
          <span>{{tableHeader.prescriptionNum}}</span>
          <span>{{medItemQr[0].wayNo}}</span>
          <span>{{medItemQr[0].usageNo}}</span>
          <span>{{tableHeader.unitPrice}}</span>
        </section>
        <section class="goods clearfix">
          <div class="box" v-for="(item, index) in medItemQr" :key="index">
            <span>{{item.drugSet.commonName}}</span>
            <span>{{item.number}}{{item.drugSet.dosegeUnit}}</span>
          </div>
        </section>
      </template>
      <template v-else>
        <section class="th">
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </section>
        <section class="td" v-for="(item, index) in medItemQr" :key="index">
          <span>{{item.drugSet.commonName}} {{item.drugSet.dosege}}{{item.drugSet.dosegeUnit}}*{{item.drugSet.divisor}}{{item.drugSet.doseUnit}}/{{item.drugSet.saleUnit}}</span>
          <span>{{item.number}}</span>
          <span>{{item.wayNo}}</span>
          <span>{{item.dose?item.dose:''}}{{item.doseUnit?item.doseUnit:""}}{{item.dose?'/':''}}{{item.usageNo}}</span>
        </section>
      </template>
      <div class="sum">
        
        <i>{{orderAmount}}</i>
      </div>
      <div class="button" @click="goBack()"></div>
    </main>
    <div class="fill"></div>
  </div>
</template>
<script>
import base from "../assets/js/base.js";
import headerBar from "@/components/header-bar";
import { ImagePreview } from "vant";
export default {
  name: "medicalHistoryDetail",
  data() {
    return {
      isShow: true,
      detail: {},
      medRecord: {},
      imgArr: [],
      singlePost: null,
      obj: {},
      showArr: [],
      tableHeader: {},
      sum: "",
      medItemQr: [],
      orderAmount: null
    };
  },
  methods: {
    goBack() {
      this.isShow = true;
    },
    look() {
      this.isShow = false;
    },
    imgShow() {
      ImagePreview({
        images: this.showArr
      });
    },
    getData() {
      base
        .postRequest("/ptInfo/selectMedOrderInfo", {
          medOrderNo: this.$route.query.medOrderNo,
          medRecordNo: this.$route.query.medRecordNo
        })
        .then(res => {
          if (res.status == 0) {
            this.detail = res.data.ptInfoQr;
            this.medRecord = res.data.medRecord;
            this.imgArr = res.data.mrPictures;
            if (this.imgArr) {
              let newArr = this.imgArr.map(ele => {
                return ele.mrPictureAddress;
              });
              this.showArr = newArr;
              console.log(this.showArr);
            }

            this.tableHeader = res.data.medOrder;
            this.singlePost = res.data.medOrder.orderAmount;
            this.orderAmount = res.data.mallOrder.orderAmount;
            this.medItemQr = res.data.medItemQr;
          } else {
            base.toast(res.msg);
            this.noDataShow = true;
          }
        });
    }
  },
  filters: {
    ageFilter(e) {
      return base.sumAge(e);
    },
    timeFilter(e) {
      if (e) {
        return base.timeSwitch(e);
      }
    }
  },
  components: {
    "header-bar": headerBar
  },
  created() {
    this.getData();
  },
  mounted() {
    base.changeTabbar("#medicalHistory");
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
.header {
  position: relative;
  top: ptr(44px);
  padding: ptr(10px) ptr(20px);
  font-size: ptr(14px);
  color: #666;
  background-color: #fff;
  border-bottom: 1px solid #f4f4f4;
  section {
    display: flex;
    display: -webkit-flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: ptr(5px);
    .name {
      color: #333;
      font-size: ptr(16px);
      width: ptr(80px);
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
}
main {
  position: relative;
  top: ptr(44px);
  padding: ptr(10px) ptr(20px);
  background-color: #fff;
  h3 {
    font-size: ptr(16px);
    padding-bottom: ptr(10px);
  }
  p {
    font-size: ptr(14px);
    padding: 0 0 ptr(10px) ptr(15px);
    color: #666;
  }
  .th {
    color: #fff;
    padding: ptr(5px);
    display: flex;
    font-size: ptr(14px);
    display: -webkit-flex;
    align-items: center;
    justify-content: space-between;
    background-color: #0cb0b5;
    span {
      width: 25%;
      text-align: center;
    }
    .first {
      width: 15%;
    }
    .second {
      width: 35%;
      text-align: left;
      padding: 0 ptr(2px);
    }
  }
  .td {
    padding: ptr(5px);
    display: flex;
    display: -webkit-flex;
    align-items: center;
    font-size: ptr(14px);
    justify-content: space-between;
    border: 1px solid #f4f4f4;
    border-top: none;
    span {
      color: #666;
      width: 25%;
      text-align: center;
    }
    .first {
      width: 15%;
    }
    .second {
      width: 35%;
      text-align: left;
      padding: 0 ptr(2px);
    }
  }
  .goods {
    border: 1px solid #f4f4f4;
    border-top: none;
    font-size: ptr(14px);
    .box {
      float: left;
      color: #999;
      width: 25%;
      padding: ptr(5px) 0;
      text-align: center;
      span {
        text-align: center;
        width: 50%;
      }
    }
  }
  .sum {
    font-size: ptr(16px);
    text-align: right;
    padding: ptr(10px);
    i {
      color: #f44;
      font-size: ptr(18px);
    }
  }
  .button {
    background-color: #0cb0b5;
    color: #fff;
    width: ptr(200px);
    margin: ptr(20px) auto;
    border-radius: ptr(4px);
    text-align: center;
    height: ptr(35px);
    font-size: ptr(14px);
    line-height: ptr(35px);
  }
  img {
    width: ptr(40px);
    height: ptr(40px);
    margin-right: ptr(10px);
  }
}
.look {
  background-color: #0cb0b5;
  color: #fff;
  width: ptr(200px);
  margin: ptr(20px) auto;
  border-radius: ptr(4px);
  text-align: center;
  height: ptr(35px);
  font-size: ptr(14px);
  line-height: ptr(35px);
}
.fill {
  position: relative;
  top: ptr(44px);
  height: ptr(60px);
}
</style>




