<template>
  <div>
    <ul class="doctor-box" v-if="isShow">
      <li class="img">
        <img src="@/assets/img/u60.png" alt="">
      </li>
      <li class="info">
        <section>
          <span class="name">{{info.name}}</span>
          <span class="position">{{info.title}}</span>
        </section>
        <section>
          <span class="department">{{info.chiefNo}}</span>
          <span class="hospital">{{info.hospital}}</span>
        </section>
      </li>
    </ul>
    <main v-if="isShow">
      <ul class="information-content clearfix" v-for="(item, index) in list" :key="index">
        <li class="doctor-information" v-if="item.messageKind==1">
          <section>
            <span class="img">
              <img src="@/assets/img/u60.png" alt="">
            </span>
            <span class="textContent">
              {{item.messageContent}}
              <i>{{item.createTime}}</i>
            </span>
          </section>
        </li>
        <li class="my-information" v-else>
          <section>
            <span class="textContent">
               {{item.messageContent}}
              <i>{{item.createTime}}</i>
            </span>
            <span class="img">
              <img src="@/assets/img/user.jpg" alt="">
            </span>
          </section>
        </li>
      </ul>
      <div class="fill"></div>
    </main>
    <div class="input-box" v-if="isShow">
      <input type="text" placeholder="" v-model="messageContent">
      <span class="send" @click="send()"></span>
    </div>
    <div class="noData" v-if="isMessage"></div>
  </div>
</template>
<script>
import base from "../assets/js/base.js";
export default {
  name: "information",
  data() {
    return {
      list:[],
      info:{},
      messageContent: "",
      isShow: false,
      isMessage:false
    };
  },
  methods: {
    send() {
      if (!this.messageContent) {
        base.toast("");
        return;
      }
      base
        .postRequest("/zlyyptbcjk/insertMsgByPtNo", {
          drNo: JSON.parse(sessionStorage.getItem("doctorInfo")).drNo,
          ptNo: JSON.parse(sessionStorage.getItem("userinfo")).ptNo,
          messageContent: this.messageContent
        })
        .then(res => {
          if (res.status == 0) {
            this.messageContent = "";
            base.toast(res.msg);
            this.getData();
          } else {
            base.toast(res.msg);
          }
        });
    },
    getData() {
      base
        .getAjax("/zlyyptbcjk/selectMsgByPtNo", {
          drNo: JSON.parse(sessionStorage.getItem("doctorInfo")).drNo,
          ptNo: JSON.parse(sessionStorage.getItem("userinfo")).ptNo
        })
        .then(res => {
        if (res.status == 0) {
            this.isShow = true;
            if (res.data.length > 0) {
            this.isMessage=false;
            } else {
            this.isMessage=true;
            }
            this.list = res.data;
          } else {
            this.isShow=false;
            base.toast(res.msg);
          }
        });
    }
  },
  created(){
    if(sessionStorage.getItem("doctorInfo")){
       this.info=JSON.parse(sessionStorage.getItem('doctorInfo'));
       this.isShow=true;
       this.getData();
    }else{
       this.isShow=false;
    }
  },
  mounted() {
    base.changeTabbar("#information");
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
.doctor-box {
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  display: -webkit-flex;
  padding: ptr(10px);
  box-sizing: border-box;
  background-color: #fff;
  .img {
    width: ptr(50px);
    img {
      height: ptr(40px);
      width: ptr(40px);
    }
  }
  .info {
    flex: 1;
    section {
      margin-bottom: ptr(5px);
    }
    section:last-child {
      margin-bottom: 0;
    }
    .name {
      font-size: ptr(14px);
      font-weight: bold;
      margin-right: ptr(5px);
    }
    .position,
    .department,
    .hospital {
      font-size: ptr(12px);
      color: #666;
    }
    .department {
      margin-right: ptr(5px);
    }
  }
}
main {
  position: relative;
  top: ptr(65px);
  padding: ptr(10px);
  li {
    margin-bottom: ptr(10px);
  }
  .doctor-information {
    section {
      display: flex;
      .img {
        img {
          width: ptr(30px);
          height: ptr(30px);
          border-radius: ptr(4px);
        }
      }
      .textContent {
        position: relative;
        display: inline-block;
        margin-left: ptr(10px);
        width: ptr(220px);
        font-size: ptr(14px);
        border-radius: ptr(4px);
        padding: ptr(5px);
        background-color: #fff;
        i {
          margin-top: ptr(5px);
          display: block;
          color: #999;
          font-size: ptr(10px);
        }
      }
      .textContent::before {
        position: absolute;
        content: "";
        top: ptr(10px);
        left: ptr(-5px);
        height: ptr(10px);
        width: ptr(10px);
        background-color: #fff;
        border-radius: ptr(2px);
        transform: rotate(45deg);
      }
    }
  }
  .my-information {
    float: right;
    section {
      display: flex;
      .img {
        img {
          width: ptr(30px);
          height: ptr(30px);
          border-radius: ptr(4px);
        }
      }
      .textContent {
        position: relative;
        display: inline-block;
        margin-right: ptr(10px);
        width: ptr(220px);
        font-size: ptr(14px);
        border-radius: ptr(4px);
        padding: ptr(5px);
        color: #fff;
        background-color: #07c160;
        i {
          margin-top: ptr(5px);
          display: block;
          color: #eee;
          font-size: ptr(10px);
        }
      }
      .textContent::after {
        position: absolute;
        content: "";
        top: ptr(10px);
        right: ptr(-5px);
        height: ptr(10px);
        width: ptr(10px);
        background-color: #07c160;
        border-radius: ptr(2px);
        transform: rotate(45deg);
      }
    }
  }
}
.input-box {
  position: fixed;
  display: flex;
  display: -webkit-flex;
  bottom: ptr(60px);
  width: 100%;
  background-color: #fff;
  padding: ptr(10px);
  height: ptr(50px);
  box-sizing: border-box;
  input {
    flex: 1;
    background-color: #eee;
    border-radius: ptr(4px);
    height: ptr(30px);
    outline: none;
    border: none;
    padding: 0 ptr(5px);
    margin-right: ptr(10px);
  }
  .send {
    display: flex;
    display: -webkit-flex;
    align-items: center;
    padding: ptr(5px) ptr(10px);
    border-radius: ptr(4px);
    color: #fff;
    background-color: #07c160;
  }
}

.noData {
  padding-top: ptr(50px);
  font-size: ptr(16px);
  color: #999;
  text-align: center;
}
.fill {
  height: ptr(110px);
  width: 100%;
}
</style>



