<template>
  <div>
    <ul
      class="doctor-box"
      v-for="(item, index) in list"
      :key="index"
      @click.stop="jumpPage('doctorDetail',item.drNo,index)"
    >
      <li class="img">
        <img src="@/assets/img/u60.png" alt="">
        <span class="tip" v-if="item.newMessage>0">{{item.newMessage>99?'99':item.newMessage}}</span>
      </li>
      <li class="info">
        <section>
          <span class="name">{{item.drName}}</span>
          <span class="position">{{item.title}}</span>
        </section>
        <section>
          <span class="department">{{item.chiefNo}}</span>
          <span class="hospital">{{item.hospital}}</span>
        </section>
        <section class="it">
          <span class="inquiry" @click.stop="jumpPage('inquiry',item.drNo,index)"></span>
        </section>
      </li>
    </ul>
    <div class="noData" v-if="noDataShow"></div>
    <div class="fill"></div>
  </div>
</template>
<script>
import base from "../assets/js/base.js";
export default {
  name: "myDoctor",
  data() {
    return {
      page: 1,
      scrollSwitch: true,
      list: [],
      noDataShow: false
    };
  },
  methods: {
    jumpPage(url, drNo, index) {
      let doctorInfo = this.list[index];
      sessionStorage.setItem("doctorInfo", JSON.stringify(doctorInfo));

      if (url == "inquiry") {
        this.addCase(drNo);
      } else {
        setTimeout(() => {
          this.$router.push({ path: url, query: { drNo: drNo } });
        }, 200);
      }
    },
    getData() {
      base
        .postRequest("/ptInfo/selectDrList", {
          page: this.page,
          rows: 15
        })
        .then(res => {
          if (res.status == 0) {
            if (res.data.list.length > 0) {
              this.list = [...this.list, ...res.data.list];
            } else {
              this.scrollSwitch = false;
            }
            if (this.list.length == 0) {
              this.noDataShow = true;
            } else {
              this.noDataShow = false;
            }
          } else {
            base.toast(res.msg);
          }
        });
    },
    getUser() {
      base.postAjax("/ptInfo/selectInfo").then(res => {
        if (res.status == 0) {
          sessionStorage.setItem("userinfo", JSON.stringify(res.data));
        } else {
          base.toast(res.msg);
        }
      });
    },
    addCase(drNo) {
      base
        .postRequest("/ptInfo/start", {
          drNo: drNo
        })
        .then(res => {
          if (res.status == 0) {
            let medRecordNo = res.data;
            this.$router.push({
              path: "inquiry",
              query: { drNo: drNo, medRecordNo: medRecordNo }
            });
          } else {
            base.toast(res.msg);
          }
        });
    }
  },
  created() {
    this.page = 1;
    this.list=[];
    this.scrollSwitch = true;
    this.getUser();
    this.getData();
  },
  mounted() {
    base.changeTabbar("#myDoctor");
    window.addEventListener("scroll", () => {
      if (
        base.getDocumentHeight() ==
          base.getClientHeight() + base.getScrollTop() &&
        this.scrollSwitch
      ) {
        this.page++;
        this.getData();
      }
    });
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
.doctor-box {
  display: flex;
  display: -webkit-flex;
  padding: ptr(10px);
  background-color: #fff;
  border-bottom: 1px solid #f4f4f4;
  .img {
    position: relative;
    width: ptr(80px);
    img {
      height: ptr(60px);
      width: ptr(60px);
      border-radius: 50%;
    }
    .tip {
      position: absolute;
      top: ptr(5px);
      font-size: ptr(12px);
      text-align: center;
      color: #fff;
      right: ptr(8px);
      height: ptr(20px);
      vertical-align: middle;
      line-height: ptr(20px);
      width: ptr(20px);
      border-radius: 50%;
      background-color: #f44;
    }
  }
  .info {
    flex: 1;
    section {
      margin-bottom: ptr(5px);
    }
    section:last-child {
      margin-bottom: 0;
    }
    .name {
      font-size: ptr(16px);
      font-weight: bold;
      margin-right: ptr(5px);
    }
    .position,
    .department,
    .hospital {
      font-size: ptr(12px);
      color: #666;
    }
    .department {
      margin-right: ptr(5px);
    }
    .inquiry {
      display: inline-block;
      padding: ptr(4px) ptr(20px);
      color: #0cb0b5;
      border-radius: ptr(4px);
      margin-right: ptr(10px);
      border: 1px solid #0cb0b5;
    }
  }
}
.noData {
  padding-top: ptr(50px);
  font-size: ptr(16px);
  color: #999;
  text-align: center;
}
.fill {
  height: ptr(60px);
  width: 100%;
}
</style>



