<template>
  <footer >
    <router-link to="/myDoctor" @click.native="tabClick(0)" id="myDoctor">
      <i class="iconfont iconyisheng"></i>
      <span></span>
    </router-link>
        <router-link to="/medicalHistory" @click.native="tabClick(1)" id="medicalHistory">
      <i class="iconfont iconbingli"></i>
      <span></span>
    </router-link>
    <router-link to="/myPrescription" @click.native="tabClick(2)" id="myPrescription">
      <i class="iconfont icontext"></i>
      <span></span>
    </router-link>
    <router-link to="/mine" @click.native="tabClick(3)" id="mine">
      <i class="iconfont iconwode1"></i>
      <span></span>
    </router-link>
  </footer>
</template>

<script>
import base from "../assets/js/base.js";
export default {
  name: "footer-bar",
  data() {
    return {

    };
  },
  methods: {
    /***tabbar***/
    tabClick(val) {
      let dom = document.querySelectorAll("footer a");
      for (let i = 0; i < dom.length; i++) {
        dom[i].style.color = "#949494";
      }
      dom[val].style.color = "#0CB0B5";
    }
  },
  watch: {
    $route(to, from) {
      if (to.name == "myPrescription") {
        this.isShow = true;
        base.changeTabbar("#myPrescription");
      } else if (to.name == "Mine") {
        this.isShow = true;
        base.changeTabbar("#mine");
      } else if (to.name == "myDoctor") {
        this.isShow = true;
        base.changeTabbar("#myDoctor");
      } else if (to.name == "medicalHistory") {
        this.isShow = true;
        base.changeTabbar("#medicalHistory");
      } else if (to.name == "information") {
        this.isShow = true;
        base.changeTabbar("#information");
      }
    }
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/scss/base.scss";
footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  display: flex;
  align-items: center;
  height: ptr(60px);
  font-size: ptr(14px);
  background-color: #fff;
  box-shadow: 0 3px 14px 2px rgba(0, 0, 0, 0.12);
}

footer a {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 25%;
  height: 100%;
  color: #949494;
}

footer i {
  height: ptr(35px);
  width: ptr(35px);
  text-align: center;
  line-height: ptr(35px);
  font-size: ptr(28px) !important;
}

.textActive {
  color: #f56c02;
}
</style>



