<template>
  <div class="order">
    <header-bar></header-bar>
    <main>
      <template v-if="tableHeader.orderType==1">
        <section class="th">
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </section>
        <section class="td">
         <span>{{tableHeader.prescriptionNum}}</span>
          <span>{{medItemQr[0].wayNo}}</span>
          <span>{{medItemQr[0].usageNo}}</span>
          <span>{{tableHeader.unitPrice}}</span>
        </section>
        <section class="goods clearfix">
          <div class="box" v-for="(item, index) in medItemQr" :key="index">
            <span>{{item.drugSet.commonName}}</span>
            <span>{{item.number}}{{item.drugSet.dosegeUnit}}</span>
          </div>
        </section>
      </template>
      <template v-else>
        <section class="th">
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </section>
        <section class="td" v-for="(item, index) in medItemQr" :key="index">
          <span>{{item.drugSet.commonName}} {{item.drugSet.dosege}}{{item.drugSet.dosegeUnit}}*{{item.drugSet.divisor}}{{item.drugSet.doseUnit}}/{{item.drugSet.saleUnit}}</span>
          <span>{{item.number}}</span>
          <span>{{item.wayNo}}</span>
          <span>{{item.dose?item.dose:''}}{{item.doseUnit?item.doseUnit:""}}{{item.dose?'/':''}}{{item.usageNo}}</span>
        </section>
      </template>
      <section class="option">
        <span></span>
        <span @click="isType=true">
          {{type}}
          <i class="iconfont iconright"></i>
        </span>
      </section>
      <section class="option">
        <span></span>
        <span class="freight">{{freight}}</span>
      </section>
      <div class="sum">
        
        <i>{{orderAmount}}</i>
      </div>
    </main>
    <div class="address" @click="open()" v-if="addressShow">
      <div class="info">
        <section v-if="address && address.name">
          <span>{{address.name}}</span>
          <span>{{address.phone}}</span>
        </section>
        <section class="noAdress" v-if="isNo"></section>
        <div
          v-if="address"
          class="address-detail"
        >{{address.province}}{{address.city}}{{address.county}}{{address.addressDetail}}</div>
      </div>
      <div class="iconfont iconright"></div>
    </div>
    <div class="address-modal" v-if="isShow">
      <div class="bg" @click="close()"></div>
      <div class="content">
        <div class="list">
          <div
            class="info-list"
            :class="[item.mark=='1'?'active':'']"
            v-for="(item, index) in addressList"
            :key="index"
            @click="onSelect(item)"
          >
            <section>
              <span>{{item.name}}</span>
              <span>{{item.phone}}</span>
            </section>
            <div
              :class="[item.mark=='1'?'active':'address-detail']"
            >{{item.province}}{{item.city}}{{item.county}}{{item.addressDetail}}</div>
          </div>
          <div class="nodata" v-if="isNo"></div>
        </div>
        <div class="add" @click="add()"></div>
      </div>
    </div>
    <!---->
    <van-popup v-model="isType" position="bottom" :style="{ height: '18%' }">
      <van-radio-group v-model="typeValue" @change="typeChange">
        <van-cell-group>
          <van-cell title="" clickable @click="typeValue = ''">
            <van-radio slot="right-icon" name=""/>
          </van-cell>
          <van-cell title="" clickable @click="typeValue = ''">
            <van-radio slot="right-icon" name=""/>
          </van-cell>
        </van-cell-group>
      </van-radio-group>
    </van-popup>
    <div class="button" @click="pay()"></div>
    <div class="fill"></div>
  </div>
</template>
<script>
import base from "../assets/js/base.js";
import { Dialog } from "vant";
import headerBar from "@/components/header-bar";
export default {
  name: "confirmOrder",
  data() {
    return {
      tableHeader: {},
      medItemQr: [],
      detail: {},
      addressList: [],
      address: {},
      sum: 0,
      isShow: false,
      isNo: false,
      orderAmount: null,
      singlePost: null, //
      isType: false,
      type: "",
      freight: 0,
      postage: 0,
      typeValue: "",
      medRecord: {},
      imgArr: [],
      showArr: [],
      addressShow: false
    };
  },
  methods: {
    pay() {
      let that = this;
      if (this.type == "" && this.addressList.length <= 0) {
        base.toast("");
        return;
      }
      base
        .postRequest("/buy/updateShippingStatus", {
          address: this.address.addId,
          mallNo: this.mallNo,
          shippingStatus: this.type == "" ? 2 : 1
        })
        .then(res => {
          if (res.status == 0) {
            that.getParameter();
          } else {
            base.toast(res.msg);
          }
        });
    },
    //
    getParameter() {
      base
        .getAjax("/buy/wxpay", {
          mallNo: this.mallNo
        })
        .then(res => {
          this.weixinPay(res);
        });
    },
    //
    weixinPay(res) {
      let that = this;
      wx.config({
        debug: false, // ,apialertpclogpc
        appId: res.appid, // 
        timestamp: res.timestamp, // 
        nonceStr: res.nonceStr, // 
        signature: res.signature, // 
        jsApiList: ["chooseWXPay"] // JS
      });
      wx.ready(function() {
        // configreadyconfigconfigreadyready
        wx.chooseWXPay({
          timestamp: res.timestamp, // jssdktimestamptimeStampS
          nonceStr: res.nonceStr, //  32 
          package: res.package, // prepay_idprepay_id=\*\*\*
          signType: res.signType, // 'SHA1''MD5'
          paySign: res.signature, // 
          success: function() {
            that.setPayStatus();
          }
        });
      });
      wx.error(function() {
        // configerrorconfigdebugresSPA
        that.pay();
      });
    },
    //
    setPayStatus() {
      base
        .postRequest("/buy/notice", {
          mallNo: this.mallNo
        })
        .then(res => {
          if (res.status == 0) {
            base.toast("");
            setTimeout(() => {
              this.$router.push("myPrescription");
            }, 1000);
          } else {
            base.toast(res.msg);
          }
        });
    },
    //
    getAdress() {
      base.getAjax("/ptInfo/selectReceiptAddress").then(res => {
        if (res.status == 0) {
          if (res.data.length > 0) {
            this.addressList = res.data;
            this.address = res.data.filter(ele => {
              return ele.mark == "1";
            })[0];
            this.isNo = false;
          } else {
            this.isNo = true;
          }
        } else {
          base.toast(res.msg);
        }
      });
    },
    getData() {
      base
        .postRequest("/ptInfo/selectMedOrderInfo", {
          medOrderNo: this.$route.query.medOrderNo,
          medRecordNo: this.$route.query.medRecordNo
        })
        .then(res => {
          if (res.status == 0) {
            this.detail = res.data.ptInfoQr;
            this.medRecord = res.data.medRecord;
            this.imgArr = res.data.mrPictures;
            if (this.imgArr) {
              let newArr = this.imgArr.map(ele => {
                return ele.mrPictureAddress;
              });
              this.showArr = newArr;
            }
            this.singlePost = res.data.medOrder.orderAmount;
            this.tableHeader = res.data.medOrder;
            this.mallNo = res.data.mallOrder.mallNo;
            this.orderAmount = res.data.mallOrder.orderAmount;
            this.sum = res.data.mallOrder.orderAmount;
            this.medItemQr = res.data.medItemQr;
            if (this.medItemQr.length == 1) {
              this.getFreight(this.medItemQr[0].drugSet.drugSetNo);
            } else if (this.medItemQr.length > 1) {
              this.getFreight(0);
            }
          } else {
            base.toast(res.msg);
            this.noDataShow = true;
          }
        });
    },
    //
    getFreight(e) {
      base
        .postRequest("/drugset/selectPostage", {
          drugSetNo: e
        })
        .then(res => {
          if (res.status == 0) {
            let reg=new RegExp(res.data[0].condition)
            if (reg.test(this.medItemQr[0].number)) {
              this.postage = res.data[0].postage;
            } else {
              this.postage = 0;
            }
          } else {
            base.toast(res.msg);
          }
        });
    },
    open() {
      this.isShow = true;
    },
    close() {
      this.isShow = false;
    },
    add() {
      this.$router.push("addAddress");
    },
    onSelect(item) {
      let data = item;
      data.mark = 1;
      base
        .postAjax("/ptInfo/updateReceiptAddress", JSON.stringify(data))
        .then(res => {
          if (res.status == 0) {
            this.isShow = false;
            this.getAdress();
          } else {
            base.toast(res.msg);
          }
        });
    },
    typeChange(e) {
      this.type = e;
      if (e == "") {
        this.getAdress();
        this.freight = this.postage;
        this.addressShow = true;
      } else {
        this.freight = 0;
        this.addressShow = false;
      }
      this.orderAmount = base.accAdd(
        parseFloat(this.sum),
        parseInt(this.freight)
      );
      this.isType = false;
    }
  },
  created() {
    this.getData();
  },
  components: {
    "header-bar": headerBar
  },
  mounted() {
    base.changeTabbar("#myPrescription");
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
.address {
  position: relative;
  top: ptr(54px);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: ptr(10px) ptr(20px);
  background-color: #fff;
  border-bottom: 1px dotted #f4f4f4;
  .info {
    flex: 1;
    font-size: ptr(14px);
    span {
      margin-right: ptr(20px);
    }
    .noAdress {
      font-size: ptr(30px);
      color: #f44;
    }
    .address-detail {
      color: #666;
      margin-top: ptr(10px);
    }
  }
  .iconfont {
    font-size: ptr(20px);
    text-align: right;
    width: ptr(40px);
  }
}
.option {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: ptr(10px) ptr(20px);
  background-color: #fff;
  span {
    color: #333;
    font-size: ptr(14px);
  }
  .freight {
    padding-right: ptr(16px);
  }
}
.option:first-of-type {
  padding-top: ptr(40px);
}
.address-modal {
  .bg {
    position: absolute;
    top: 0;
    z-index: 99;
    height: 100%;
    width: 100%;
    opacity: 0.5;
    background-color: #999;
  }
  .content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    z-index: 999;
    padding: ptr(10px);
    height: ptr(460px);
    width: ptr(300px);
    border-radius: ptr(8px);
    background-color: #fff;
    .list {
      height: ptr(400px);
      overflow: auto;
      .info-list {
        padding: ptr(10px);
        font-size: ptr(14px);
        border-bottom: 1px solid #f4f4f4;
        span {
          margin-right: ptr(20px);
        }
        .address-detail {
          color: #666;
          margin-top: ptr(5px);
        }
        .address-active {
          color: #0cb0b5;
          margin-top: ptr(5px);
        }
      }
      .nodata {
        text-align: center;
        color: #999;
        font-size: ptr(14px);
      }
    }
    .active {
      color: #0cb0b5;
    }
    .add {
      margin-top: ptr(20px);
      height: ptr(40px);
      line-height: ptr(40px);
      color: #0cb0b5;
      font-size: ptr(14px);
      text-align: center;
      border-radius: ptr(4px);
      border: 1px solid #0cb0b5;
    }
  }
}
main {
  position: relative;
  top: ptr(44px);
  padding: ptr(10px) ptr(20px);
  background-color: #fff;
  .th {
    color: #fff;
    padding: ptr(5px);
    display: flex;
    font-size: ptr(14px);
    display: -webkit-flex;
    align-items: center;
    justify-content: space-between;
    background-color: #0cb0b5;
    span {
      width: 25%;
      text-align: center;
    }
  }
  .td {
    padding: ptr(5px);
    display: flex;
    display: -webkit-flex;
    align-items: center;
    font-size: ptr(14px);
    justify-content: space-between;
    border: 1px solid #f4f4f4;
    border-top: none;
    span {
      color: #666;
      width: 25%;
      text-align: center;
    }
  }
  .goods {
    border: 1px solid #f4f4f4;
    border-top: none;
    font-size: ptr(14px);
    margin-bottom: ptr(20px);
    span {
      float: left;
      color: #999;
      width: 25%;
      padding: ptr(5px) 0;
      text-align: center;
    }
  }
  .sum {
    font-size: ptr(16px);
    text-align: right;
    padding: ptr(10px);
    i {
      font-size: ptr(18px);
      color: #f44;
    }
  }
}
.button {
  position: relative;
  top: ptr(54px);
  background-color: #0cb0b5;
  color: #fff;
  width: ptr(200px);
  margin: ptr(20px) auto;
  border-radius: ptr(4px);
  text-align: center;
  height: ptr(35px);
  font-size: ptr(14px);
  line-height: ptr(35px);
}
.fill {
  position: relative;
  top: ptr(44px);
  height: ptr(60px);
  width: 100%;
}
</style>




